import { cssProps } from '@oracle/oraclejet-cssprops';
import { MessageVariant } from './Message.types';
import { formatTimestamp } from './MessageFormattingUtils';

const messageTimestampStyles = {
  banner: cssProps`${{
    flexShrink: 0,
    fontSize: 'var(--oj-c-PRIVATE-DO-NOT-USE-typography-body-xs-font-size)',
    color: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-text-color-secondary)'
  }}`,
  inline: undefined
};

/**
 * Props for the Timestamp component
 */
type Props = {
  /**
   * The timestamp to show in ISO format
   */
  value: string;

  /**
   * Style variant
   */
  variant?: MessageVariant;
};

/**
 * Timestamp Component for rendering timestamp in Message
 */
function MessageTimestamp({ value, variant = 'banner' }: Props) {
  const classes = `oj-c-message${variant}-timestamp ${messageTimestampStyles[variant]}`;

  // Otherwise, render the timestamp
  const formattedTimestamp = formatTimestamp(value as string);
  return <div class={classes}>{formattedTimestamp}</div>;
}

export { MessageTimestamp };
